import { MyLib } from "@macashipo/mlib"
import React, { useEffect } from "react"
import styled from "styled-components"

const Wrap = styled.div`
  border: 1px solid #ebebeb;
  height: calc(100vh - 10rem);
  & > .react-codemirror2 {
    height: 100%;
    & > .CodeMirror-wrap {
      height: 100%;
    }
  }
`

function PrettyJsonString(jsonString) {
  let _s = jsonString
  try {
    _s = JSON.stringify(JSON.parse(jsonString), null, 2)
  } catch (error) {}
  return _s
}
export default function MUIJsonViewer(props) {
  const { data } = props
  return (
    <Wrap>
      <MyLib
        type={"code_mirror"}
        value={PrettyJsonString(data || "")}
        options={{
          // mode: "javascript",
          mode: {
            name: "javascript",
            json: true,
            statementIndent: 2,
          },
          lineNumbers: true,
          lineWrapping: true,
          indentWithTabs: false,
          tabSize: 2,
          theme: "default",
        }}
      />
    </Wrap>
  )
}
